@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', sans-serif;
  background-color: rgba(240, 240, 240, .3);
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  color: white;
}

.catalog {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 95%;
  max-width: 1240px;
  margin: 20px auto;
}

.catalog__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 20px 10px;
  font-size: 12px;
}

.catalog__loader {
  align-self: center;
}

.catalog__buttons-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  margin-top: 20px;
  align-self: center;
  outline: none;
  padding: 10px 30px;
  font-size: 28px;
  background-color: orange;
  color: white;
  border: none;
  transition: .3s;
  cursor: pointer;
  border-radius: 3px;
  color: brown;
}

.button:hover {
  background-color: brown;
  color: orange;
}

.button--smaller {
  font-size: 14px;
  padding: 5px;
  margin: 5px;
  width: 65px;

}

.button--active,
.button--active:hover {
  background-color: orangered;
  color: white;
}

.button--active {
  transition: 0.3s;
}

.card {
  display: flex;
  flex-direction: column;
  padding: 5px 10px;
  margin: 20px 15px;
  box-shadow: 0px 0px 4px 2px #ccc;
  transition: .3s;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 10px;
}

.card:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.card--in-popup:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.card__img {
  margin: 5px 0 10px;
  width: 245px;
  align-self: center;
}

.card__acapit {
  margin: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__type-name:last-child {
  padding-bottom: 10px;
}

.card__type {
  font-weight: bold;
}

.card__type--bigger {
  font-size: 16px;
}

@media (min-width: 800px) {
  .card--in-popup {
    order: 2;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.header__title {
  font-size: 34px;
  margin: 0 20px 40px 0;
  color: orange;
}

.header__title--brown {
  color: brown;
}

@media (min-width: 550px) {
  .catalog__header {
    justify-content: space-between;
  }
}

.form__input {
  font-family: "Lato", sans-serif;
  width: 200px;
  border: none;
  padding-bottom: 5px;
  font-size: 14px;
  outline: none;
  background-color: transparent;
  color: white;
}

.form__input::-webkit-input-placeholder {
  color: white;
}

.form__input:-ms-input-placeholder {
  color: white;
}

.form__input::-ms-input-placeholder {
  color: white;
}

.form__input::placeholder {
  color: white;
}

.form__input-subtitle {
  padding-top: 5px;
  font-size: 14px;
  height: 35px;
}

.form__line {
  height: 2px;
  background-color: orange;
}

.form__line--color {
  background-color: brown;
}

.popup {
  display: flex;
  position: fixed;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  max-height: 100%;
  margin: 0 auto;
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.popup__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  flex-basis: 95vh;
}

.popup__fight-details {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 10px 0;
  width: 95%;
  max-width: 600px;
}

.popup__title {
  font-size: 24px;
  margin: 60px 0 20px;
}

@media (min-width:443px) {
  .popup__title {
    font-size: 34px;
    margin: 40px 0 20px;
  }
}

@media (min-width: 800px) {
  .popup__fight-details {
    order: 1;
  }
}

.popup__detail {
  width: 200px;
  margin: 10px 5px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 0px 4px 2px #ccc;
}

.popup__detail-name {
  display: grid;
  grid-template-columns: 1fr 30px;
  width: 100%;
  margin-top: 20px;
  font-size: 20px;
}

.popup__detail-type {
  font-size: 24px;
  font-weight: bold;
}

.popup__detail-type::first-letter {
  text-transform: uppercase;

}

.popup__button {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  outline: none;
  background-color: transparent;
  border: none;
}

.error {
  font-size: 34px;
  text-align: center;
}

